<template>
    <div>
      <mbread :isOpen="true" :breadSecond="breadSecond" :breadname="breadname"> </mbread>
       <el-row style="margin-bottom: 10px">
         <el-col :span="14" :xs="24" :lg="8" style="margin: 5px auto">
           <span>查看人员:</span>
           <el-select v-model="value"  placeholder="所有" @change="change">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
         </el-col>
         <el-col :span="4" :xs="24" :lg="8">
           <el-button type="success" style="margin: 5px auto" @click="addPerson">增加员工</el-button>
         </el-col>
         <el-col :span="6" :xs="24" :lg="8" style="margin: 5px auto">
            <el-input  v-model="searchName" suffix-icon="el-icon-search" class="search" @change="handleSearch" placeholder="按姓名查找"></el-input>
         </el-col>
       </el-row>

      <el-table :data="pageData" border width="95%">
            <el-table-column
              prop="name"
              label="姓名">
            </el-table-column>
            <el-table-column
              prop="Depart"
              label="部门">
            </el-table-column>
            <el-table-column
              prop="Position"
              label="职位">
            </el-table-column>
            <el-table-column
              prop="tel"
              label="电话">
            </el-table-column>
            <el-table-column
              label="操作"
              width="100">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                <el-button type="text" size="small">删除</el-button>
              </template>
            </el-table-column>
          </el-table>

      <el-row>
        <el-col :span="24" :xs="6"> .
          <el-pagination
          background
          layout="total,prev, pager, next"
          :total="total" :current-page="1"  @current-change="changePage" >
         </el-pagination>
        </el-col>
      </el-row>

      <!-- 模态框-->
      <el-dialog
        title="增加员工"
        :visible.sync="dialogVisible"
      width="60%">
        <el-form :model="form1" >
          <el-form-item label="姓名" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form1.name" auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="登录账号" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form1.userName" auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="登录密码" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form1.userPwd" auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="用户权限" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form1.access" placeholder="请选择权限" >
              <el-option label="管理员" value="0"></el-option>
              <el-option label="部长" value="1"></el-option>
              <el-option label="副部长" value="2"></el-option>
              <el-option label="员工" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所在部门" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form1.Depart" placeholder="请选择部门" >
              <el-option label="总部" value="0"></el-option>
              <el-option label="支援部" value="1"></el-option>
              <el-option label="报告部" value="2"></el-option>
              <el-option label="生产部" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="职位" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form1.Position" placeholder="请选择职位" >
              <el-option label="普工" value="0"></el-option>
              <el-option label="部长" value="1"></el-option>
              <el-option label="思密达" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="电话" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form1.tel" maxlength="11"  class="input1" ></el-input>
          </el-form-item>
          <el-form-item label="时间" size="mini" :label-width="formLabelWidth">
            <el-date-picker
              v-model="date"
              type="datetime" class="input1"
              placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="Save">确 定</el-button>
        </span>
      </el-dialog>

      <!--查看模态框-->
      <el-dialog title="查看信息"  :visible.sync="dialogVisible1" width="60%" >
        <el-form v-model="form2">
          <el-form-item label="姓名" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form2.name" disabled auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="登录账号" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form2.userName" disabled auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="登录密码" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form2.userPwd"  disabled auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="用户权限" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form2.access" disabled placeholder="请选择权限" >
              <el-option label="管理员" value="0"></el-option>
              <el-option label="部长" value="1"></el-option>
              <el-option label="副部长" value="2"></el-option>
              <el-option label="员工" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所在部门" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form2.Depart"  disabled placeholder="请选择部门" >
              <el-option label="总部" value="0"></el-option>
              <el-option label="支援部" value="1"></el-option>
              <el-option label="报告部" value="2"></el-option>
              <el-option label="生产部" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="职位" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form2.Position" disabled placeholder="请选择职位" >
              <el-option label="普工" value="0"></el-option>
              <el-option label="部长" value="1"></el-option>
              <el-option label="思密达" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="电话" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form2.tel" maxlength="11" disabled class="input1" ></el-input>
          </el-form-item>
          <el-form-item label="注册日期" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form2.registDate" disabled  class="input1"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible1 = false">取 消</el-button>
          <el-button type="primary" @click="Edit">编辑</el-button>
        </span>
      </el-dialog>

      <!--编辑模态框-->
      <el-dialog title="修改信息"  :visible.sync="dialogVisible3" width="60%" >
        <el-form v-model="form3">
          <el-form-item label="姓名" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form3.name"  auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="登录账号" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form3.userName"  auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="登录密码" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form3.userPwd"   auto-complete="off" class="input1"></el-input>
          </el-form-item>
          <el-form-item label="用户权限" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form3.access"  placeholder="请选择权限" >
              <el-option label="管理员" value="0"></el-option>
              <el-option label="部长" value="1"></el-option>
              <el-option label="副部长" value="2"></el-option>
              <el-option label="员工" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所在部门" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form3.Depart"   placeholder="请选择部门" >
              <el-option label="总部" value="0"></el-option>
              <el-option label="支援部" value="1"></el-option>
              <el-option label="报告部" value="2"></el-option>
              <el-option label="生产部" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="职位" size="mini" :label-width="formLabelWidth">
            <el-select  v-model="form3.Position"  placeholder="请选择职位" >
              <el-option label="普工" value="0"></el-option>
              <el-option label="部长" value="1"></el-option>
              <el-option label="思密达" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="电话" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form3.tel" maxlength="11"  class="input1" ></el-input>
          </el-form-item>
          <el-form-item label="注册日期" size="mini" :label-width="formLabelWidth">
            <el-input v-model="form3.registDate"   class="input1"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible3= false">取 消</el-button>
          <el-button type="primary" @click="saveEdit">修改</el-button>
        </span>
      </el-dialog>
    </div>
</template>

<script>
  import mbread from '@/components/header/mbread'
  import axios from 'axios'

    export default {
        name: "person-manager",
        data() {
            return {
              breadSecond: '管理',
              breadname: '人员管理',
              options:[
                {
                  value: '0',
                  label: '所有'
                },
                {
                  value: '1',
                  label: '生产部'
                },{
                  value: '2',
                  label: '支援部'
                },{
                  value: '3',
                  label: '报告部'
                },{
                  value: '4',
                  label: '运营部'
                }
              ],
              value: '',
              /*
              *  是表格数据
              * */
              pageData: [],
              allData: {},
              total: 0,
              data1: [],
              initData: [],
              /*
              *  查找姓名
              * */
              searchName:'',
              /*显示模态框*/
              dialogVisible: false,
              dialogVisible1: false,
              dialogVisible3: false,
              form1:{
                name: '',
                Depart: '',
                Position: '',
                tel: '',
                userName: '',
                userPwd: '',
                access: '',
                registDate: ''
              },
              date:'',
              formLabelWidth: '40%',

              form2:{},
              isDisabled: false,
              form3:{},

            }
        },
      components:{

        mbread
      },
      mounted(){
          this._initGetData();
      },
      methods:{
        _initGetData(){
          axios.get('/users/getAllUsers').then((response) => {
            let res = response.data;
            if (res.status === '0'){
              this.allData = res.result;
              this.total = res.result.length;
              for (let i = 0; i < res.result.length; i++) {
                let list = {};
                list.userId = res.result[i].userId;
                list.name = res.result[i].name;
                list.Depart = res.result[i].Depart;
                list.Position = res.result[i].Position;
                list.access = String (res.result[i].access);
                list.tel = res.result[i].tel;
                list.userName = res.result[i].userName;
                list.userPwd = res.result[i].userPwd;
                list.registDate = res.result[i].registDate;
                this.data1.push(list);
              }
              this.initData = this.data1;
              this._initPage(1);
            }else {
              alert("获取人员失败，请刷新！");
            }

          });
        },
        _initPage(current){
          this.pageData = [];
          let total = current*10;
          let min = (total-10)<=0? 0:total-10;
          for (let i = min;i<total;i++){
            if (this.data1[i]){
              this.pageData.push(this.data1[i]);
            }
          }
        },
        changePage(current){
          console.log(current);
          this._initPage(current);
        },
        /*
        *  按姓名搜索
        * */
        handleSearch(){
          console.log('21');
          this.data1 = this.initData;
          this.data1 = this.search(this.data1, {name: this.searchName});
          this.total = this.data1.length;
          this._initPage(1);
        },
        search (data, argumentObj) {
          let res = data;
          let dataClone = data;
          for (let argu in argumentObj) {
            if (argumentObj[argu].length > 0) {
              res = dataClone.filter(d => {
                return d[argu].indexOf(argumentObj[argu]) > -1;
              });
              dataClone = res;
            }
          }
          return res;
        },
        /* 查看编辑个人信息*/
        handleClick(data){
          this.form2 = data;
          this.form3 = data;
          this.dialogVisible1 = true;
        },
        /* 增加员工按钮*/
        addPerson(){
          this.dialogVisible = true;
        },
        /* 保存员工信息按钮*/
        Save(){
          console.log('保存成功!');
          this.dialogVisible = false;
        },
        Edit(){
          this.dialogVisible1 = false;
          let that = this;
          setTimeout(function () {
            that.dialogVisible3 = true;
          },300);

        },
        /* 修改成功保存信息到数据库*/
        saveEdit(){
          this.$notify({
            title: '通知',
            message: '修改成功'
          });
          this.dialogVisible3 = false;
        },
        /* 改变选择值更改table*/
        change(index){
          if (index === '0'){
            this.data1 = this.initData;
            this.total = this.data1.length;
            this._initPage(1);
          }else if (index === '1'){
            this.data1 = this.filePageData('生产部');
            this._initPage(1);
          }else if (index === '2'){
            this.data1 = this.filePageData('支援部');
            this._initPage(1);
          }else if (index === '3'){
            this.data1 = this.filePageData('报告部');
            this._initPage(1);
          }
        },
        /* 根据不同的值返回不同的pageData*/
        filePageData(name){
          let data = this.initData;
          let selectValue = data.filter(item =>{
            return item.Depart === name;
          });
          this.total = selectValue.length;
          return selectValue;
        }
      },
      computed:{

      }
    }
</script>

<style scoped>
  .el-pagination{
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 35%;
  }
  .input1{
    width: 100%;
    max-width: 300px;
  }
  .registDate{
    display: none;
  }
</style>



